import {unobserve} from '../util/unobserve';

export default {
    name: 'vx-popup',
    provide() {
        return {vxPopup: this}
    },
    model: {
        prop: 'isOpened',
        event: 'update:isOpened'
    },
    props: {
        lazyContent: {
            type: Boolean,
            default: true
        },
        overlap: {
            type: Boolean,
            default: false
        },
        modal: {
            type: Boolean,
            default: false
        },
        /**
         * 팝업을 배치하는데 기준이 되는 엘리먼트.
         * 기본값: 최상위 노드(body)
         */
        positionTarget: {
            type: [HTMLElement, String],
            default: null
        },
        /**
         * 수평 정렬 방향
         * 가능한 값은 'left', 'right', 'auto'
         * 기본값 : 'left'
         */
        horizontalAlign: {
            type: String
        },
        /**
         * 수평 정렬 방향
         * 가능한 값은 'top', 'bottom', 'auto'
         * 기본값 : 'top'
         */
        verticalAlign: {
            type: String
        },
        /**
         * 수평정렬 된 위치에 추가 되는 값
         */
        horizontalOffset: {
            type: Number,
            default: 0
        },
        /**
         * 수직정렬 된 위치에 추가 되는 값
         */
        verticalOffset: {
            type: Number,
            default: 0
        },
        /**
         *  여러개의 팝업중에 가장 최상위로 위치
         */
        alwaysOnTop: {
            type: Boolean,
            default: false,
        },
        /**
         *  여러개의 팝업중에 상위위치 우선 순위(값이 클수록 상위)
         */
        priority: {
            type: Number,
            default: 0,
        },
        closeEvents: {
            type: Array,
            default: () => ['close']
        },
        /**
         * 컨텐츠 엘리먼트 클래스 지정
         * 공백 구분으로 여러 클래스 지정가능('classA classB classC...')
         */
        contentClass: {
            type: String
        },

        autoLayout: {
            type: Boolean,
            default: false
        },
        /**
         * 팝업 닫기 대기시간.
         */
        closeDelay: {
            type: Number,
            default: 0
        },
        /**
         * 팝업이 닫힐때 팝업 인스턴스 캐쉬 삭제
         *
         */
        destroyOnClose: {
            type: Boolean,
            default: false
        },
        /**
         * 팝업 컨텐츠영역 외 클릭시 팝업 닫기 여부
         */
        closeOnOutsideClick: {
            type: Boolean,
            default: false
        },
        /**
         * 팝업 컨텐츠영역 외 마우스 다운시 팝업 닫기 여부
         */
        closeOnOutsideDown: {
            type: Boolean,
            default: false
        },
        /**
         * 'resize' 이벤트 발생시 팝업 닫기 여부.
         */
        closeOnResize: {
            type: Boolean,
            default: false
        },
        /**
         * 팝업 컨텐츠 영역외 다운, 클릭시 닫기 예외 영역
         */
        exceptionOutside: {
            type: [Array, Function]
        },
        autoFocus: {
            type: Boolean,
            default: true
        },
        isOpened: {
            type: Boolean,
            default: false
        },
        active: {
            type: HTMLElement
        }
    },
    data() {
        return {
            needContent: false,
        }
    },
    computed: {
        popupInstance() {
            this._instance = this._instance || this.createPopupInstance();
            return this._instance
        },
        positionTargetInstance() {
            const target = this.positionTarget;
            if (!target) {
                return null;
            }

            let found;
            if (target instanceof HTMLElement) {
                found = target;
            } else if (typeof target === 'string') {
                if (this.$vnode) {
                    found = this.$vnode.context.$el.querySelector(target);
                }

                if (!found) {
                    found = document.querySelector(target);
                }
            }

            if (!found) {
                throw new Error(`'positionTarget' "${target}" not found.`);
            }
            return found;
        },
    },
    watch: {
        isOpened(value) {
            if (value) {
                this.open();
            } else {
                this.close();
            }
        }
    },
    destroyed() {
        this.deactivateContent(this.getContent());
        this.popupInstance.destroy();
    },
    methods: {
        createPopupInstance() {
            const options = {
                ...this.$props,
                contentFactory: (data, options) => {
                    return new vrix.core.ProgressPromise((resolve, reject, progress) => {
                        if (this.lazyContent) {
                            if (this.needContent) {
                                resolve(this.getContent());
                            } else {
                                this.$once('hook:updated', function () {
                                    const child = this.$el.firstElementChild;
                                    if (typeof this.contentClass === 'string') {
                                        this.contentClass.split(' ').filter(name => !!name).forEach(name => child.classList.add(name))
                                    }
                                    resolve(child);
                                });
                            }
                            this.needContent = true;
                        } else {
                            resolve(this.$el.firstElementChild);
                        }
                    });
                },
                contentDestroy: (content) => {
                    if (this.destroyOnClose) {
                        this.needContent = false;
                    }
                }
            };
            return unobserve(new vrix.ui.Popup(options));
        },
        /**
         * 강제로 컨텐츠를 생성합니다.
         * @returns {Promise}
         */
        forceBuildContent() {
            return this.popupInstance.forceBuildContent(this);
        },

        /**
         * 팝업 컨텐츠 엘리먼트를 반환합니다.
         * @returns {HTMLElement}
         */
        getContent() {
            return this.popupInstance && this.popupInstance.content;
        },

        /**
         *
         * @param options
         * @returns {vrix.ui.Popup}
         */
        open(options) {
            options = {
                active: this.active,
                ...options
            };


            if (this.positionTargetInstance) {
                options.positionTarget = this.positionTargetInstance;
                if (!this.verticalAlign) {
                    options.verticalAlign = 'top';
                }

                if (!this.horizontalAlign) {
                    options.verticalAlign = 'left';
                }
            }

            return this.popupInstance.open(this, options)
                .opening(() => {
                    this.$emit('opening');
                })
                .opened(() => {
                    this.activateContent(this.getContent());
                    this.$emit('update:isOpened', true);
                    this.$emit('open');
                })
                .closed(() => {
                    this.deactivateContent(this.getContent());
                    this.$emit('update:isOpened', false);
                    this.$emit('close');
                })
        },
        /**
         *
         * @param params
         * @returns {vrix.ui.Popup}
         */
        close(params) {
            return this.popupInstance.close(params);
        },
        /**
         * @param params
         * @returns {vrix.ui.Popup}
         */
        cancel(params) {
            return this.popupInstance.cancel(params);
        },
        activateContent(content) {
            const instance = content && content.__vue__;
            if (instance) {
                const funcs = instance.$options.activated;
                if (funcs) {
                    funcs.forEach(func => func.apply(instance));
                }
                this.closeEvents.forEach((type) => {
                    instance.$on(type, this.onClosePopup);
                });
            }
        },
        deactivateContent(content) {
            const instance = content && content.__vue__;
            if (instance) {
                const funcs = instance.$options.deactivated;
                if (funcs) {
                    funcs.forEach(func => func.apply(instance));
                }
                this.closeEvents.forEach((type) => {
                    instance.$off(type, this.onClosePopup);
                });
            }
        },
        onClosePopup(event) {
            this.close(event);
        }
    },
    render(h) {
        return h('template', {
            style: {
                //ie11 template issue!!!
                display: 'none'
            }
        }, (!this.lazyContent || this.needContent) ? this.$slots.default : []);
    },
}
