import Vue from 'vue';
import Draggable from '../mixins/draggable';

export default Vue.extend({
    name: 'vx-window',
    mixins: [Draggable],
    props: {
        title: {
            type: String,
            default: ''
        },
        closeButton: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {};
    },
    computed: {},
    watch: {},
    methods: {
        genContent() {
            return [this.genHeader(), this.genBody()];
        },
        genHeader() {
            const children = [this.genIcon(), this.genTitle(), this.genCloseButton()];
            return this.$createElement('div', {
                staticClass: 'vx-window__header',
            }, children);
        },
        genIcon() {
            const slot = this.$slots.icon;
            if (!slot) {
                return null;
            }
            return this.$createElement('div', {
                staticClass: 'vx-window__header-icon',
            }, slot);
        },
        genTitle() {
            const slot = this.$slots.title;
            return this.$createElement('div', {
                staticClass: 'vx-window__header-title',
            }, slot ? slot : this.title);
        },
        genCloseButton() {
            if (!this.closeButton) {
                return null;
            }
            const slot = this.$slots.close;
            return this.$createElement('button', {
                staticClass: 'vx-window__header-close',
                on: {
                    click: this.onClick
                },
            }, slot ? slot : 'close');
        },
        genBody() {
            return this.$createElement('div', {
                staticClass: 'vx-window__body',
            }, this.$slots.default);
        },
        onClick() {
            this.$emit('close');
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-window',
        }, this.genContent());
    }
});
