import AutoCompletable from '../vxautocomplete/autocompletable';
import VxTokenItem from './VxTokenItem';
import {ComponentFactory} from '../core/ComponentWrapper';
import {unobserve} from '../util/unobserve';

const TOKEN_EVENTS = ['item-caret-change', 'item-reset', 'item-add', 'item-remove', 'select-cancel'];

export default {
    name: 'vx-tokenizer',
    provide() {
        return {tokenizer: this};
    },
    model: {
        prop: 'selectedItems',
        event: 'change'
    },
    mixins: [AutoCompletable],
    props: {
        selectedItemFactory: {
            type: Function,
            default: VxTokenItem
        },
        selectedItems: {
            type: Array,
            default: () => []
        },
        allowDuplicate: {
            type: Boolean,
            default: false
        },
        allowOnlySuggest: {
            type: Boolean,
            default: false
        },
        addOnFocusOut: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        selectedItemFactoryWrapper() {
            if (!this.selectedItemFactory) {
                throw new Error(`'selectedItemFactory' is null`);
            }
            const self = this;
            return ComponentFactory.factoryWrapper(this, this.selectedItemFactory, {
                create(data) {
                    const component = ComponentFactory.prototype.create.apply(this, [data]);
                    component.$scopedSlots = self.$scopedSlots;
                    return component;
                }
            });
        },
    },
    mounted() {
        let instance = this.instance;
        if (!instance) {
            instance = this.instance = unobserve(vrix.ui.Tokenizer.create(this.$el, {
                inputFactory: (value) => {
                    return new this.inputFactoryWrapper().element;
                },
                suggestFactory: '<div class="vx-tokenizer"><div class="suggest-list"></div></div>',
                suggestItemFactory: (item, index) => {
                    const wrapper = new this.suggestItemFactoryWrapper({
                        propsData: {
                            item,
                            index
                        }
                    });
                    return wrapper.element;
                },
                hookRenderSuggestItem: (element, item, index, state) => {
                    const vm = element.__vue__;
                    vm.search = instance.lastSuggestValue;
                    vm.index = index;
                    vm.state = state;
                },
                hookFreeSuggestItem: (element) => {
                    const vm = element.__vue__;
                    vm.$destroy();
                },
                selectedItemFactory: (item, index) => {
                    const wrapper = new this.selectedItemFactoryWrapper({
                        propsData: {
                            item, index,
                            labelField: this.labelField,
                        }
                    });
                    return wrapper.element;
                },
                hookFreeSelectedElement: (element => {
                    const vm = element.__vue__;
                    vm.$destroy();
                }),
                placeholder: this.placeholder,
                removerSelector: '.vx-tokenizer__item-remover'
            }));
        }
    },
    watch: {
        selectedItems(v) {
            if (!vrix.util.compareArray(this.instance.selectedItems, v)) {
                this.instance.selectedItems = v || [];
            }
        },
        instance(instance) {
            if (instance) {
                TOKEN_EVENTS.forEach(type => {
                    instance.addEventListener(type, this.onInstanceEvents);
                });
            }
        },
    },
    beforeDestroy() {
        if (this.instance) {
            TOKEN_EVENTS.forEach(type => {
                this.instance.removeEventListener(type, this.onInstanceEvents);
            });
        }
    },
    methods: {
        addSelectedItem(item) {
            return this.instance.addSelectedItem(item)
        },
        /**
         * 토큰 삭제
         * @param item
         */
        removeSelectedItem(item) {
            return this.instance.removeSelectedItem(item);
        },

        /**
         * 토큰 삭제
         * @param index
         */
        removeSelectedItemAt(index) {
            return this.instance.removeSelectedItemAt(index);
        },
        onInstanceEvents(event) {
            const {type} = event;
            switch (type) {
                case 'item-reset':
                case 'item-add':
                case 'item-remove':
                    this.$emit('change', this.instance.selectedItems);
                    //this.$emit('update:selectedItems', this.instance.selectedItems);
                    break;
            }
            this.$emit(type, event);
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-tokenizer'
        });
    }
}
