import Vue from 'vue';

export default Vue.extend({
    name: 'vx-token-item',
    inject: {
        tokenizer: {
            default: null
        }
    },
    props: {
        item: null,
        labelField: {
            type: [String, Function],
        },
        index: {
            type: Number
        }
    },
    computed: {
        staticClass() {
            return 'vx-tokenizer__item';
        },
        itemLabel() {
            return this.item && this.tokenizer.itemToLabel(this.item) || '';
        },
        scopedProps() {
            return {
                item: this.item,
                index: this.index,
                label: this.itemLabel,
            }
        },
    },
    methods: {
        genContent() {
            return [
                this.genLabel(),
                this.genRemover()
            ];
        },
        genLabel() {
            const children = [];
            if (this.$scopedSlots.label) {
                children.push(this.$scopedSlots.label(this.scopedProps));
            } else {
                children.push(this.itemLabel);
            }
            return this.$createElement('span', {
                staticClass: this.staticClass + '-label'
            }, children);
        },
        genRemover() {
            const children = [];
            if (this.$scopedSlots.remover) {
                children.push(this.$scopedSlots.remover(this.scopedProps));
            } else {
                children.push('x');
            }

            return this.$createElement('button', {
                staticClass: this.staticClass + '-remover',
                on: {
                    click: (event) => {
                        //this.tokenizer.removeTokenAt(this.index)
                    }
                },
            }, children);
        }
    },
    render(h) {
        return h('div', {
            staticClass: this.staticClass,
        }, this.genContent());
    }
})
