import AutoCompletable from './autocompletable';
import {unobserve} from '../util/unobserve';

export default {
    name: 'vx-auto-complete',
    mixins: [AutoCompletable],
    props: {
        value: {
            type: String,
            default: ''
        },
        clearButton: {
            type: Boolean,
            default: false,
        }
    },
    watch: {
        value(v) {
            this.instance.inputValue = v;
        },
        instance(instance) {
            if (instance) {
                if (this.value !== undefined) {
                    this.instance.inputValue = this.value;
                }
            }
        },
    },
    mounted() {
        let instance = this.instance;
        if (!instance) {
            instance = this.instance = unobserve(vrix.ui.AutoComplete.create(this.$el, {
                inputFactory: (value) => {
                    return new this.inputFactoryWrapper().element;
                },
                suggestFactory: '<div class="vx-auto-complete"><div class="suggest-list"></div></div>',
                suggestItemFactory: (item, index) => {
                    const wrapper = new this.suggestItemFactoryWrapper({
                        propsData: {
                            item: item
                        }
                    });
                    return wrapper.element;
                },
                hookRenderSuggestItem: (element, item, index, state) => {
                    const vm = element.__vue__;
                    vm.search = instance.lastSuggestValue;
                    vm.index = index;
                    vm.state = state;
                },
                hookFreeSuggestItem: (element) => {
                    const vm = element.__vue__;
                    vm.$destroy();
                },
                placeholder: this.placeholder
            }));
        }
    },
    methods: {
        onInstanceEvents(event) {
            const {type, detail} = event;
            if (type === 'input-change') {
                this.$emit('update:inputValue', detail.value);
                this.$emit('input', detail.value);
            }
            this.$emit(type, event);
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-auto-complete'
        });
    }
}
