import vue from 'vue';
import {unobserve} from '../util/unobserve';

const Properties = [
    'minScale', 'maxScale', 'scale',
    'autoPage', 'enablePan', 'hiddenScroll'
];

const Methods = [
    'setContent', 'center', 'destroy', 'invalidate'
];

const Events = [
    'scale-change', 'scroll-change',
];

const defaultProps = {};
const defaultWatch = {};
const defaultMethods = {};

function setupProp(name) {
    defaultProps[name] = undefined;
    defaultWatch[name] = function (value) {
        if (this.instance) {
            this.instance[name] = value;
        }
    };
}

Properties.forEach(name => {
    setupProp(name);
});

Methods.forEach(name => {
    defaultMethods[name] = function (...args) {
        if (this.instance) {
            return this.instance[name].apply(this.instance, args);
        }
    };
});

export default {
    provide() {
        return {scaleViewer: this};
    },
    props: {
        ...defaultProps,
    },
    data() {
        return {
            instance: null
        }
    },
    computed: {
        isPanning() {
            return !!(this.instance && this.instance.isPanning);
        },
        /* checkForSlotContent() {
             let checkForContent = (hasContent, node) => {
                 return hasContent || node.tag || (node.text && node.text.trim());
             }
             return this.$slots.default && this.$slots.default.reduce(checkForContent, false);
         },*/
    },
    mounted() {
        let instance = this.instance;
        if (!instance) {
            instance = this.instance = unobserve(vrix.ui.ScaleViewer.create(this.$el, {}));
            instance.setContent(this.$el.firstElementChild);
            instance.center()
        }
    },
    watch: {
        ...defaultWatch,
        instance(instance) {
            if (instance) {
                Events.forEach((type) => {
                    instance.addEventListener(type, this.onInstanceEvents);
                });

                const props = this.$props;
                const exceptions = [];
                for (let p in props) {
                    if (exceptions.indexOf(p) < 0) {
                        const value = props[p];
                        if (value !== undefined && !(value instanceof Function && value.prototype instanceof vue)) {
                            instance[p] = value;
                        }
                    }
                }
            }
        }
    },
    beforeDestroy() {
        if (this.instance) {
            this.instance.destroy();
        }
    },
    methods: {
        ...defaultMethods,
        onInstanceEvents(event) {
            this.$emit(event.type, event);
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-scale-viewer'
        }, this.$slots.default);
    }
}
