import vue from 'vue';
import {ComponentFactory} from '../core/ComponentWrapper';
import VxInputControl from './VxInputControl';
import VxSuggestItem from './VxSuggestItem';

const Properties = [
    'suggestSource', 'suggestCount', 'suggestFilter',
    'inputValue', 'labelField', 'minChar', 'placeholder',
    'delimiterOnKeys',
];

const Methods = [
    'clear', 'focus', 'blur', 'itemToLabel', 'toHighlightHtml', 'openSuggest', 'closeSuggest'
];

const Events = [
    'input-change',
    'suggest-open', 'suggest-close', 'suggest-select',
    'suggest-progress', 'suggest-change', 'suggest-error',
    'suggest-caret-change',
    'input-key-down', 'focus', 'blur'
];

const defaultProps = {};
const defaultWatch = {};
const defaultMethods = {};

function setupProp(name) {
    defaultProps[name] = undefined;
    defaultWatch[name] = function (value) {
        if (this.instance) {
            this.instance[name] = value;
        }
    };
}

Properties.forEach(name => {
    setupProp(name);
});

Methods.forEach(name => {
    defaultMethods[name] = function (...args) {
        if (this.instance) {
            return this.instance[name].apply(this.instance, args);
        }
    };
});

export default {
    provide() {
        return {autoComplete: this};
    },
    props: {
        ...defaultProps,
        suggestItemFactory: {
            type: Function,
            default: VxSuggestItem
        },
    },
    data() {
        return {
            instance: null
        }
    },
    computed: {
        inputFactoryWrapper() {
            return ComponentFactory.factoryWrapper(this, VxInputControl);
        },
        suggestItemFactoryWrapper() {
            if (!this.suggestItemFactory) {
                throw new Error(`'itemFactory' is null`);
            }
            const self = this;
            return ComponentFactory.factoryWrapper(this, this.suggestItemFactory, {
                create(data) {
                    const component = ComponentFactory.prototype.create.apply(this, [data]);
                    component.$scopedSlots = self.$scopedSlots;
                    return component;
                }
            });
        }
    },
    watch: {
        ...defaultWatch,
        instance(instance) {
            if (instance) {
                Events.forEach(type => {
                    instance.addEventListener(type, this.onInstanceEvents);
                });

                const props = this.$props;
                const exceptions = ['value'];
                for (let p in props) {
                    if (exceptions.indexOf(p) < 0) {
                        const value = props[p];
                        if (value !== undefined && !(value instanceof Function && value.prototype instanceof vue)) {
                            instance[p] = value;
                        }
                    }
                }
            }
        },
    },
    beforeDestroy() {
        if (this.instance) {
            const inputGroup = this.instance.inputGroup;
            if (inputGroup && inputGroup.__vue__) {
                inputGroup.__vue__.$destroy();
            }

            Events.forEach(type => {
                this.instance.removeEventListener(type, this.onInstanceEvents);
            });
            this.instance.destroy();
        }
    },
    methods: {
        ...defaultMethods,
        onInstanceEvents(event) {
            this.$emit(type, event);
        }
    }
}
