import Vue from 'vue';
import VxList from '../vxlist/VxList';
import VxDateTableItem from './VxDateTableItem';
import {DayProvider, MonthProvider, YearProvider} from './DateProvider';
import * as util from './dateutil';

export default Vue.extend({
    name: 'vx-date-table',
    provide() {
        return {dateTable: this};
    },
    props: {
        value: {
            /* 문자열은 ISO 8601만 가능*/
            type: [String, Date],
        },
        type: {
            type: String,
            default: 'day',
            validator: (type) => ['day', 'month', 'year'].find(t => t === type)
        },
        today: {
            type: Date,
            default: () => new Date()
        },
        caret: {
            type: Date,
            default: () => new Date()
        },
        weekLabels: {
            type: Array,
            default: () => util.DEFAULT_WEEK_LABELS.slice(0)
        },
        allowDates: {
            type: Function
        },
        itemFactory: {
            type: Function,
            default: VxDateTableItem
        }
    },
    data() {
        return {
            internalCaret: this.caret,
            internalSelectedDate: null,
            items: null
        };
    },
    computed: {
        providers() {
            return {
                'year': new YearProvider(this, 3, 4),
                'month': new MonthProvider(this, 3, 4),
                'day': new DayProvider(this, 7, 6)
            }
        },
        dateProvider() {
            return this.providers[this.type];
        }
    },
    watch: {
        value: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.selectDate(value, this._isMounted);
                }
            }
        },
        type(value) {
            this.updateItems();
        },
        internalCaret: {
            immediate: true,
            handler(value) {
                this.updateItems();
                this.$emit('update:caret', value);
            }
        }
    },
    methods: {
        genContent() {
            return [this.genHeader(), this.genTable()];
        },
        genHeader() {
            if (this.type !== 'day') {
                return null;
            }
            const children = this.weekLabels.map((label, index) => this.genWeekday(label, index));
            return this.$createElement('div', {
                staticClass: 'vx-date-table__header',
            }, children);
        },
        genWeekday(label, index) {
            return this.$createElement('div', {
                key: index,
                staticClass: 'vx-date-table__header-weekday',
                class: {
                    ['vx-' + util.DEFAULT_WEEK_LABELS[index].toLowerCase()]: true,
                }
            }, label);
        },
        genTable() {
            return this.$createElement(VxList, {
                staticClass: 'vx-date-table__body',
                props: {
                    itemFactory: this.itemFactory,
                    items: this.items,
                    columnCount: this.dateProvider.x,
                    rowCount: this.dateProvider.y,
                    autoHeight: true,
                    layout: 'grid'
                },
                on: {
                    'collection-change': this.onListCollectionChange,
                    'selection-changing': this.onListSelectionChanging,
                    'item-click': this.onListItemClick
                },
                ref: 'list',
            });
        },
        move(value) {
            this.items = this.dateProvider.move(value);
            this.internalCaret = this.dateProvider.current;
        },
        updateItems() {
            this.items = this.dateProvider.updateItems(this.internalCaret);
        },
        selectDate(value, isMoveCaret = true, trigger = null) {
            const toValue = this.dateProvider.constructor.toValue;
            const date = value ? util.parseDate(value) : null;

            if (value === this.internalSelectedDate || !this.validDate(date)) {
                return false;
            }

            if (date && isMoveCaret) {
                this.internalCaret = date;
            }

            if (toValue(this.internalSelectedDate) !== toValue(date)) {
                let found = date && this.dateProvider.findItem(date) || null;
                if (date) {
                    found = this.dateProvider.findItem(date);
                    if (!found) {

                    }
                }

                if (!this.$refs.list) {
                    this.$nextTick(() => {
                        if (this.$refs.list) {
                            this.$refs.list.selectedItem = found;
                        }
                    })
                } else {
                    this.$refs.list.selectedItem = found;
                }

                this.internalSelectedDate = date;
                this.$emit('selection-change', this.internalSelectedDate, trigger);
            }

            if (toValue(this.value) !== toValue(date)) {
                this.$emit('input', date);
            }
            return true;
        },
        validDate(date) {
            return !this.allowDates || this.allowDates(date);
        },
        flush() {
            const list = this.$refs.list;
            if (list) {
                list.invalidate();
                list.validateNow();
            }
        },
        onListCollectionChange(event) {
            if (event.detail.kind === 'reset') {
                if (this.internalSelectedDate) {
                    this.$nextTick(() => {
                        if (this.$refs.list) {
                            this.$refs.list.selectedItem = this.dateProvider.findItem(this.internalSelectedDate);
                        }
                    });
                }
            }
        },
        onListSelectionChanging(event) {
            const item = event.detail.selectedItemTo;
            if (item && !this.validDate(item.source)) {
                event.preventDefault();
            }
        },
        onListItemClick(event) {
            const date = event.detail.item.source;
            this.$emit(event.type, event);
            if (!event.defaultPrevented) {
                this.selectDate(date, true, event);
            }
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-date-table',
            class: {
                [`vx-date-table--year--${this.type}`]: true
            }
        }, this.genContent());
    }
});
