import {compareArray} from '../../../util/utils';

export default {
    name: 'vx-selector-group',
    props: {
        value: null,
        /**
         * 선택 가능한 이벤트 목록
         */
        selectOnEvents: {
            type: Array,
            default: () => ['click']
        },
        /**
         * 선택시 'trueAs' or 'falseAs' 설정 할 속성 이름
         * 그룹 구성 엘리먼트가 HTMLElement 이면 attribute 로 접근,
         * 이외 오브젝트는 property 로 접근합니다.
         */
        selectedField: {
            type: String,
            default: 'selected'
        },
        /**
         * 'true' 표시하는 값
         */
        trueAs: {
            type: [String, Boolean],
            default: true
        },
        /**
         * 'false' 표시하는 값
         */
        falseAs: {
            type: [String, Boolean],
            default: false
        },
        valueField: {
            type: String,
            default: null
        },
    },
    data() {
        return {
            vnodes: [],
            selected: null,
            selectedValue: null
        };
    },
    computed: {
        boundChildEvents() {
            return this.onChildEvents.bind(this);
        }
    },
    watch: {
        value(v) {
            if (this.valueField && this.selectedValue !== v) {
                this.selected = this.vnodes.find(node => this.getValue(node) === v) || null;
            }
        },
        vnodes(nodes, old) {
            if (Array.isArray(old)) {
                old.forEach(node => this.removeListener(node));
            }

            //초기 세팅
            nodes.forEach(node => {
                this.addListener(node);
            });

            this.$nextTick(() => {
                this.selected = nodes.find(node => {
                    if (this.valueField && this.value) {
                        return this.getValue(node) === this.value;
                    }
                    return this.isSelect(node);
                }) || null;
            })
        },
        selected(node) {
            this.vnodes.forEach(selector => {
                this.setSelect(selector, node === selector);
            });

            this.selectedValue = this.getValue(node);
            this.$emit('input', this.valueField && node ? this.selectedValue : node);
            this.$emit('change', {
                node: node,
                element: node && (node.componentInstance || node.elm),
                index: this.vnodes.indexOf(node),
                value: this.selectedValue
            });
        }
    },
    mounted() {
        this.updateVNodes();
        this.$once('hook:beforeDestroy', () => {
            this.vnodes || [].forEach(node => this.removeListener(node))
        });
    },
    updated() {
        this.updateVNodes();
    },
    methods: {
        updateVNodes() {
            const children = this._vnode && this._vnode.children;
            if (Array.isArray(children)) {
                const nodes = children.filter(node => node.tag);
                if (!compareArray(this.vnodes, nodes)) {
                    this.vnodes = nodes;
                }
            }
        },
        addListener(node) {
            const element = node.componentInstance || node.elm;
            this.selectOnEvents.forEach(name => {
                if (element instanceof HTMLElement) {
                    element.addEventListener(name, this.boundChildEvents);
                } else if (element) {
                    element.$on(name, this.boundChildEvents);
                }
            });
        },
        removeListener(node) {
            const element = node.componentInstance || node.elm;
            this.selectOnEvents.forEach(name => {
                if (element instanceof HTMLElement) {
                    element.removeEventListener(name, this.boundChildEvents);
                } else if (element) {
                    element.$off(name, this.boundChildEvents);
                }
            });
        },
        getValue(node) {
            if (this.valueField && node) {
                const field = this.valueField;
                const element = node.componentInstance || node.elm;
                if (element instanceof HTMLElement) {
                    return element.getAttribute(field);
                } else if (element && field in element) {
                    return element[field];
                }
            }
            return null;
        },
        isSelect(node) {
            const field = this.selectedField;
            const element = node.componentInstance || node.elm;
            if (element instanceof HTMLElement) {
                return element.hasAttribute(field);
            } else if (element && field in element) {
                return element[field] === this.trueAs;
            }
            return false;
        },
        /**
         * 노드 선택 지정및 해제
         * @param node
         * @param isSelect
         */
        setSelect(node, isSelect) {
            const field = this.selectedField;
            const element = node.componentInstance || node.elm;
            if (element instanceof HTMLElement) {
                if (isSelect) {
                    element.setAttribute(field, this.trueAs);
                } else {
                    element.removeAttribute(field);
                }
            } else if (element && field in element) {
                element[field] = isSelect ? this.trueAs : this.falseAs;
            }
        },
        onChildEvents(event) {
            this.selected = this.vnodes.find(selector => {
                return selector.elm === event.currentTarget;
            });
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-selector-group',
        }, this.$slots.default);
    },
}
