import Vue from 'vue'
import VxDatePickerDay from './VxDatePickerDay';
import VxDatePickerYear from './VxDatePickerYear';
import VxDatePickerMonth from './VxDatePickerMonth';
import {DEFAULT_WEEK_LABELS, toYYYYMMDD, parseDate, toDateString, equals as equalsDate} from '../vxdatetable/dateutil';

const DATE_PATH = ['day', 'month', 'year'];
export default Vue.extend({
    props: {
        value: {
            /* 문자열은 ISO 8601만 가능*/
            type: [String, Date]
        },
        type: {
            type: String,
            default: 'day',
            validator: (type) => ['day', 'month', 'year'].find(v => v === type)
        },
        today: {
            type: Date,
            default: () => new Date()
        },
        weekLabels: {
            type: Array,
            default: () => DEFAULT_WEEK_LABELS.slice(0)
        },
        allowDates: {
            type: Function
        },
        titleFormat: {
            type: Function,
            default: (type, date, items) => {
                const yyyymmdd = '' + toYYYYMMDD(date);
                switch (type) {
                    case 'year' :
                        return items ? `${items[0].value}-${items[items.length - 1].value}` : '';
                    case 'month':
                        return `${yyyymmdd.substr(0, 4)}`;
                }

                return `${yyyymmdd.substr(0, 4)}-${yyyymmdd.substr(4, 2)}`;
            }
        },
        format: {
            type: String
        },
        selectedDate: {
            type: Date
        }
    },
    data() {
        return {
            dateItems: null,
            internalType: this.type,
            internalCaret: new Date(),
            internalSelectedDate: null
        };
    },
    computed: {
        headerTitle() {
            return this.titleFormat(this.internalType, this.internalCaret, this.dateItems);
        }
    },
    watch: {
        value: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.selectDate(parseDate(value, this.format));
                    this.valueChanged = true;
                }
            }
        },
        selectedDate: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.selectDate(value);
                    this.valueChanged = true;
                }
            }
        }
    },
    methods: {
        genContent() {
            return [this.genControl(), this.genTable()]
        },
        genControl() {
            const children = [this.genPrev(), this.genTitle(), this.genNext()];
            return this.$createElement('div', {
                staticClass: 'vx-date-picker__control',
            }, children);
        },
        genPrev() {
            const slot = this.$slots.prev;
            return this.$createElement('div', {
                staticClass: 'vx-date-picker__control-prev',
                on: {
                    'click': this.prev
                }
            }, slot ? slot : '<');
        },
        genNext() {
            const slot = this.$slots.next;
            return this.$createElement('div', {
                staticClass: 'vx-date-picker__control-next',
                on: {
                    'click': this.next
                }
            }, slot ? slot : '>');
        },
        genTitle() {
            const slot = this.$slots.title;
            return this.$createElement('div', {
                staticClass: 'vx-date-picker__control-title',
                on: {
                    'click': this.onTitleClick
                }
            }, slot ? slot() : this.headerTitle);
        },
        genTable() {

            let component = null;
            if (this.internalType === 'day') {
                component = VxDatePickerDay;
            } else if (this.internalType === 'month') {
                component = VxDatePickerMonth;
            } else {
                component = VxDatePickerYear;
            }

            return this.$createElement(component, {
                staticClass: 'vx-date-picker__body',
                props: {
                    ...this.$props,
                    value: this.internalSelectedDate,
                    caret: this.internalCaret,
                    type: this.internalType,
                },
                ref: 'table',
                on: {
                    'input': this.onDateTableInput,
                    'update:caret': this.onDateTableCaretUpdate,
                    'item-click': this.onDateTableEvents,
                    'item-down': this.onDateTableEvents,
                    'item-up': this.onDateTableEvents,
                    'selection-change': this.onDateTableSelectionChange,
                },
                scopedSlots: this.$scopedSlots,
            });
        },
        getSelectedDate() {
            return this.internalSelectedDate;
        },
        getSelectedFormatDate() {
            if (this.internalSelectedDate) {
                return this.format && toDateString(this.internalSelectedDate, this.format) || this.internalSelectedDate.toISOString();
            }
            return null;
        },
        selectDate(date) {
            if (date) {
                if (!equalsDate(this.internalCaret, date, this.type)) {
                    this.internalCaret = date;
                }
            }

            if (!equalsDate(this.internalSelectedDate, date, this.type)) {
                this.internalSelectedDate = date;
            }
        },
        move(value) {
            if (this.$refs.table) {
                this.$refs.table.move(value);
            }
        },
        prev() {
            this.move(-1);
        },
        next() {
            this.move(+1);
        },
        /**
         * 상위이동(day -> month, month -> year)
         */
        moveTop(date) {
            this.lockedCall(() => {
                const index = DATE_PATH.findIndex(type => type === this.internalType);
                if (index + 1 < DATE_PATH.length) {
                    this.internalType = DATE_PATH[index + 1];
                    if (date) {
                        this.internalCaret = date;
                    }
                }
            });
        },
        /**
         * 하위 이동(year -> month, month -> day)
         */
        moveBottom(date) {
            this.lockedCall(() => {
                const index = DATE_PATH.findIndex(type => type === this.internalType);
                if (index - 1 >= 0) {
                    this.internalType = DATE_PATH[index - 1];
                    if (date) {
                        this.internalCaret = date;
                    }
                }
            });
        },
        lockedCall(callback) {
            if (!this.locked) {
                this.locked = true;
                callback();
                this.$nextTick(() => {
                    this.locked = false;
                });
            }
        },
        flush() {
            this.$refs.table.flush();
        },
        onDateTableInput(date) {
            if (this.type === this.internalType) {
                const value = this.format ? toDateString(date, this.format) : date;
                this.$emit('input', value);
                this.$emit('update:selectedDate', date);
            }
        },
        onDateTableCaretUpdate(date) {
            this.$nextTick(() => {
                if (this.$refs.table) {
                    this.internalCaret = date;
                    this.dateItems = this.$refs.table.items;
                }
            });
        },
        onDateTableSelectionChange(date) {
            if (this.type === this.internalType && (this.internalSelectedDate !== date || this.valueChanged)) {
                this.internalSelectedDate = date;
                this.$emit('selection-change', date);
                this.$emit('update:selectedDate', date);
            }
        },
        onDateTableEvents(event) {
            const type = event.type;
            if (type === 'item-click') {
                this.valueChanged = false;
                if (this.type !== this.internalType) {
                    this.moveBottom(event.detail.item.source);
                    event.preventDefault();
                }
            }
            this.$emit(type, event);
        },
        onTitleClick(event) {
            this.valueChanged = false;
            this.moveTop();
        },
        onMousewheel(event) {
            this.$nextTick(() => {
                if (event.wheelDelta > 0) {
                    this.prev();
                } else {
                    this.next();
                }
            });
            event.preventDefault();
            return false;
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-date-picker',
            on: {
                'mousewheel': this.onMousewheel,
            }
        }, this.genContent());
    }
});
