function extend(target, ...options) {
    const prototype = target.prototype;
    options.forEach(option => {
        Object.getOwnPropertyNames(option).forEach(name => {
            Object.defineProperty(prototype, name, Object.getOwnPropertyDescriptor(option, name));
        });
    });
    return target;
}

export class ComponentWrapper {
    constructor(comp) {
        this._compoenent = comp;
    }

    get component() {
        return this._compoenent;
    }

    get element() {
        return this.component.$el;
    }
}

export class ComponentFactory {
    constructor(parent, wrapper) {
        this.parent = parent;
        this.wrapper = wrapper;
    }

    create(data) {
        const component = new this.wrapper({
            parent: this.parent,
            ...data,
        });
        component.$mount();
        return component;
    }


    static factoryWrapper(parent, wrapper, extendMethods) {
        let Factory;
        if (extendMethods) {
            Factory = extend((class extends ComponentFactory {
                constructor() {
                    super(parent, wrapper);
                }
            }), extendMethods);
        } else {
            Factory = ComponentFactory;
        }

        const factory = new Factory(parent, wrapper);
        return class VueComponentWrapper extends ComponentWrapper {
            constructor(data) {
                super(factory.create(data))
            }
        }
    }
}
