import Listable from '../mixins/listable';
import ListItem from './VxListItem';
import {unobserve} from '../util/unobserve';

export default Listable.extend({
    name: 'vx-list',
    props: {
        itemFactory: {
            type: Function,
            default: ListItem
        }
    },
    data() {
        return {
            needUpdate: false
        }
    },
    watch: {
        needUpdate(value) {
            if (value) {
                this.invalidate();
            }
        }
    },
    mounted() {
        let instance = this.instance;
        if (!instance) {
            instance = this.instance = unobserve(vrix.ui.List.create(this.$el, {
                itemFactory: (item, index) => {
                    let wrapper = new this.itemFactoryWrapper({
                        propsData: {
                            item: item,
                            labelField: this.labelField,
                            valueField: this.valueField,
                        },
                        staticClass: this.itemStaticClass
                    });
                    if (this.autoSize) {
                        wrapper.component.$once('hook:updated', (event) => {
                            this.updateElementSize(wrapper.element, item, index);
                        });
                    }
                    return wrapper.element;
                },
                renderItemCallback: (element, item, index, state) => {
                    const vm = element.__vue__;
                    vm.index = index;
                    vm.state = state;
                },
                freeItemCallback: (element) => {
                    const vm = element.__vue__;
                    vm.$destroy();
                }
            }));

            instance.addEventListener('render-updating', (event) => {
                if (this.autoSize) {
                    this.needUpdate = false;
                }
            });

            instance.addEventListener('render-update', (event) => {
                if (this.autoSize) {
                    if (this.needUpdate) {
                    }
                }
            });
        }
    },
    beforeDestroy() {
        if (this.instance) {
            this.instance = null;
        }
    },
    methods: {
        updateElementSize(element, item, index) {
            const instance = this.instance;
            if (!this.needUpdate && instance.calculateElementWidth(element, item, index) !== instance.getItemSize(index)) {
                this.needUpdate = true;
            }
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-list'
        });
    }
})
