export default {
    name: 'list-drop-indicator',
    inject: {
        owner: {
            default: null
        }
    },
    props: {},
    methods: {
        genContent() {
            return this.$slots.default;
        },

        showDropIndicator(detail) {
            let LINE_HEIGHT = 10;
            let tree = this.owner;

            let collection = tree.collection;
            let dropPosition = detail.dropPosition;

            let indent = tree.indent || 40;

            let dropIndex = dropPosition.dropIndex;
            let dropItem = dropPosition.dropItem;
            let insertParentItem = dropPosition.insertParentItem;
            let insertIndex = dropPosition.insertIndex;
            let level = collection.getNodeLevel(insertParentItem);

            let insertLevel;
            let yy;
            let hh;

            if (dropItem === insertParentItem && (!tree.isOpenNode(dropItem) || tree.hasChildren(dropItem) || insertIndex === 0)) {
                let dropHeight = tree.getItemSize(dropIndex);
                hh = dropHeight - LINE_HEIGHT;
                yy = Math.round(tree.getItemStart(dropIndex) + ((dropHeight - hh) / 2));
                insertLevel = level;
            } else {
                let children = tree.getChildren(insertParentItem);
                let index;
                if (children.length <= insertIndex) {
                    if (dropIndex === collection.length - 1) {
                        index = dropIndex + 1;
                    } else {
                        index = collection.indexOf(children[children.length - 1]) + 1;
                    }
                } else {
                    index = collection.indexOf(children[insertIndex]);
                }

                hh = LINE_HEIGHT;
                yy = Math.round(tree.getItemStart(index) - (hh / 2));
                insertLevel = level + 1;
            }
            let style = this.$el.style;
            style.top = yy + 'px';
            style.height = hh + 'px';
            style.right = 0 + 'px';
            style.left = Math.max(0, insertLevel * indent) + 'px';
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'vx-indicator',
        }, this.genContent());
    }
}
