import ListItem from '../vxlist/VxListItem.js';

export default ListItem.extend({
    name: 'vx-tree-item',
    props: {
        indent: {
            type: Number,
            default: 0
        }
    },
    computed: {
        staticClass() {
            return 'vx-tree__item';
        },
        itemAttrs() {
            return {
                selected: this.state.selected,
                active: this.state.active,
                isOpened: this.state.isOpened
            }
        },
        calculatedLeft() {
            let left = (this.state.level || 0) * this.indent;
            return left + 'px';
        }
    },
    methods: {
        genContent() {
            return [
                this.genToggle(),
                this.genIcon(),
                this.genLabel()
            ]
        },
        genToggle() {
            const children = [];
            if (this.$scopedSlots.toggle) {
                children.push(this.$scopedSlots.toggle(this.scopedProps));
            } else {
                children.push(this.state.isOpened ? '-' : '+');
            }

            return this.$createElement('div', {
                staticClass: this.staticClass + '__toggle',
                class: {
                    'toggle--open': this.state.isOpened,
                },
                style: {
                    visibility: this.state.hasChildren ? '' : 'hidden'
                },
                on: {
                    click: (event) => {
                        event.stopPropagation();
                        this.owner.toggleNode(this.item);
                    }
                },
            }, children);
        }
    },
    render(h) {

        const style = {};
        if (this.indent > 0) {
            style.left = this.calculatedLeft;
        }
        return h('div', {
            staticClass: this.staticClass,
            class: {
                [`vx-level-${this.state.level}`]: true,
                ['vx-leaf']: !this.state.hasChildren,
            },
            style,
            attrs: this.itemAttrs,
        }, this.genContent());
    }
})
