import VxPopup from '../vxpopup/VxPopup'

export default {
    name: 'dropdownable',
    props: {
        placeholder: {
            type: String,
            default: ''
        },
        maxWidth: {
            type: Number,
        },
        minWidth: {
            type: Number,
        },
        /**
         * 아이템 선택 후 팝업 닫기 여부
         */
        closeOnSelect: {
            type: Boolean,
            default: true
        },
        /**
         * 팝업 컨텐츠영역 외 클릭시 팝업 닫기 여부
         */
        closeOnOutsideClick: {
            type: Boolean,
            default: false
        },
        /**
         * 팝업 컨텐츠영역 외 마우스 다운시 팝업 닫기 여부
         */
        closeOnOutsideDown: {
            type: Boolean,
            default: true
        },

        /**
         * 팝업 컨텐츠 클래스
         *
         */
        contentClass: {
            type: String
        }
    },
    data() {
        return {
            isOpened: false,
            isOpening: false,
            selectedItems: null
        };
    },
    computed: {
        prefixClassName() {
            return this.$options.name;
        },
        /**
         * 선택된 아이템이 있는지 여부입니다.
         * @returns {boolean}
         */
        hasSelections() {
            return !!(this.selectedItems && this.selectedItems.length > 0);
        },
        popupContentClass() {
            const name = (this.$attrs.name || '').trim();
            const contentClass = (this.contentClass || '').trim();
            return name + (name ? ' ' : '') + contentClass;
        }
    },
    watch: {
        isOpened(value) {
            if (value) {
                this.isOpening = true;
                this.$refs.dropdown.open({active: this.$el})
                    .opened(content => {
                        const found = content && content.querySelector('[tabindex]');
                        if (found) {
                            // use 'setActive' in IE/Edge so container does not scroll...
                            if (found.setActive) {
                                found.setActive(); // IE/Edge
                            } else {
                                found.focus(); // All other browsers
                            }
                        }
                        this.isOpened = true;
                        this.$nextTick(() => {
                            this.isOpening = false;
                        });
                    })
                    .closed(result => {
                        this.isOpened = false;
                        this.isOpening = false;
                    });
            } else {
                this.$refs.dropdown.close();
            }
            this.$emit('update:isOpened', this.isOpened);
        },
        selectedItems(value) {
            if (this.closeOnSelect && this.isOpened && !this.isOpening) {
                this.$nextTick(this.close);
            }
        }
    },
    methods: {
        calculatedMinWidth() {
            if (this.isOpened) {

            }

            let minWidth = this.minWidth;
            if (isNaN(minWidth) && this.$el) {
                minWidth = this.$el.offsetWidth;
            }
            if (isNaN(this.maxWidth)) {
                return minWidth;
            }
            return Math.min(this.maxWidth, minWidth);
        },
        open() {
            if (!this.isOpened) {
                this.isOpened = true;
            }
        },
        close() {
            if (this.isOpened) {
                this.isOpened = false;
            }
        },
        itemToLabel(item) {
            return item;
        },
        itemToValue(item) {
            return item;
        },
        genContent() {
            return this.genControl();
        },
        genControl() {
            const children = [];
            children.push(this.hasSelections ? this.genSelections() : this.genPlaceholder());
            children.push(this.genDropdownTrigger());
            return this.$createElement('div', {
                staticClass: `${this.prefixClassName}__control`,
                on: {
                    mousedown: this.onControlMouseDown,
                    mouseup: this.onControlMouseUp,
                    click: this.onControlClick
                },
                ref: 'control'
            }, children);
        },
        genSelections() {
            const children = this.selectedItems.map(item => this.genSelection(item));
            return this.$createElement('div', {
                staticClass: `${this.prefixClassName}__selections`,
            }, children);
        },
        genSelection(item) {
            const slot = this.$scopedSlots.selection;
            return this.$createElement('div', {
                staticClass: `${this.prefixClassName}__selection`,
            }, slot ? slot(item) : this.itemToLabel(item));
        },
        genPlaceholder() {
            const placeholder = this.placeholder || '';
            if (placeholder.length <= 0) {
                return null;
            }
            const slot = this.$scopedSlots.placeholder;
            return this.$createElement('div', {
                staticClass: `${this.prefixClassName}__placeholder`
            }, slot ? slot() : placeholder);
        },
        genDropdownTrigger() {
            const slot = this.$scopedSlots.trigger;
            return this.$createElement('div', {
                staticClass: `${this.prefixClassName}__dropdown-trigger`
            }, slot ? slot() : '열기');
        },
        genDropdown() {
            const dropdownContent = this.genDropdownContent();
            return this.$createElement(VxPopup, {
                props: {
                    positionTarget: `.${this.prefixClassName}__control`,
                    closeOnOutsideDown: this.closeOnOutsideDown,
                    closeOnResize: true,
                    destroyOnClose: false,
                    contentClass: this.popupContentClass,
                    active: this.$el
                },
                on: {
                    'opening': this.onDropdownOpening
                },
                ref: 'dropdown',
            }, [dropdownContent]);
        },
        genDropdownContent() {
            return null;
        },
        onControlMouseDown(event) {
            this.$emit('mousedown', event);
        },
        onControlMouseUp(event) {
            this.$emit('mouseup', event);
        },
        onControlClick(event) {
            const target = event.target;
            if (this.isOpened) {
                const control = this.$refs.control;
                if (control === target || control.contains(target)) {
                    this.$nextTick(this.close);
                }
            } else {
                this.open();
            }
            this.$emit('click', event);
        },
        onDropdownOpening(event) {

        }
    },
    render(h) {
        const children = [this.genContent(), this.genDropdown()];
        return h('div', {
            staticClass: this.prefixClassName,
        }, children);
    }
}
