import Dropdownable from '../mixins/dropdownable'
import VxDatePicker from '../vxdatepicker/VxDatePicker'
import {toYYYYMMDD, parseDate} from '../vxdatetable/dateutil'

export default {
    name: 'vx-date-field',
    mixins: [Dropdownable],
    props: {
        ...VxDatePicker.options.props,
        selectedFormat: {
            type: Function,
            default: (type, date) => {
                const yyyymmdd = '' + toYYYYMMDD(date);
                switch (type) {
                    case 'year' :
                        return `${yyyymmdd.substr(0, 4)}`;
                    case 'month':
                        return `${yyyymmdd.substr(0, 4)}-${yyyymmdd.substr(4, 2)}`;
                }
                return `${yyyymmdd.substr(0, 4)}-${yyyymmdd.substr(4, 2)}-${yyyymmdd.substr(6, 2)}`;
            },
        }
    },
    watch: {
        value: {
            immediate: true,
            handler(value) {
                if (value !== undefined) {
                    this.selectDate(parseDate(value, this.format));
                }
            }
        },
        selectedDate: {
            immediate: true,
            handler(value, old) {
                if (value !== undefined) {
                    this.selectDate(value);
                }
            }
        }
    },
    methods: {
        getSelectedDate() {
            return this.$refs.datePicker && this.$refs.datePicker.getSelectedDate();
        },
        getSelectedFormatDate() {
            return this.$refs.datePicker && this.$refs.datePicker.getSelectedFormatDate();
        },
        itemToLabel(date) {
            return this.selectedFormat && this.selectedFormat(this.type, date) || date;
        },
        selectDate(date) {
            if (!this.$refs.datePicker) {
                this.$nextTick(() => {
                    if (this.$refs.dropdown) {
                        this.$refs.dropdown.forceBuildContent();
                    }
                });
            } else {
                this.$refs.datePicker.selectDate(date);
            }
        },
        genDropdownContent() {
            return this.$createElement(VxDatePicker, {
                staticClass: 'vx-date-field__dropdown-content',
                props: {
                    ...this.$props
                },
                on: {
                    'input': this.onDatePickerInput,
                    'selection-change': this.onDatePickerSelectionChange,
                    'item-click': this.onDatePickerItemEvents,
                    'item-down': this.onDatePickerItemEvents,
                    'item-up': this.onDatePickerItemEvents,
                },
                scopedSlots: this.$scopedSlots,
                ref: 'datePicker'
            });
        },
        onDatePickerSelectionChange(date) {
            //선택 후 닫힘.
            this.selectedItems = date ? [date] : [];
            this.$emit('selection-change', date);
        },
        onDatePickerInput(date) {
            this.$emit('input', date);
        },
        onDropdownOpening() {
            this.$refs.datePicker.flush();
        },
        onDatePickerItemEvents(event) {
            this.$emit(event.type, event);
        },
    }
}
