import * as dateFormatter from 'date-format';

export const DEFAULT_WEEK_LABELS = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

export function cloneDate(date) {
    return new Date(date.getTime());
}

/**
 * 해당 월의 시작 주의 첫날을 반환합니다.
 * @param date
 * @returns {Date}
 */
export function getMonthStart(date) {
    const start = new Date(date.getFullYear(), date.getMonth(), 1);
    start.setDate(-start.getDay() + 1);
    return start;
}

/**
 * 해당 월의 마지막 주의 첫날을 반환합니다.
 * @param date
 * @returns {Date}
 */
export function getMonthEnd(date) {
    const end = new Date(date.getFullYear(), date.getMonth() + 1, 1);
    end.setDate((7 - end.getDay()));
    return end;
}

export function toYYYY(date) {
    if (date instanceof Date) {
        return date.getFullYear();
    }
    return 0;
}

export function toYYYYMM(date) {
    if (date instanceof Date) {
        return (date.getFullYear() * 100) + (date.getMonth() + 1);
    }
    return 0;
}

export function toYYYYMMDD(date) {
    if (date instanceof Date) {
        return toYYYYMM(date) * 100 + date.getDate();
    }
    return 0;
}

export function parseDate(value, format = null) {
    if (value instanceof Date) {
        return value;
    } else if (typeof value === 'string') {

        let result = null;
        if (format) {
            result = toDate(value, format);
        } else {
            result = Date.parse(value);
        }

        if (isNaN(result)) {
            throw new Error(`Invalid Date. value is '${value}'`);
        }
        return new Date(result);
    }
    return null;
}

export function toDate(value, format = 'yyyy.MM.dd') {
    return dateFormatter.parse(format, value);
}

export function toDateString(date, format = 'yyyy.MM.dd') {
    return dateFormatter.asString(format, date)
}

export function equals(d1, d2, type) {
    if (type === 'year') {
        return toYYYY(d1) === toYYYY(d2);
    } else if (type === 'month') {
        return toYYYYMM(d1) === toYYYYMM(d2);
    }
    return toYYYYMMDD(d1) === toYYYYMMDD(d2);
}
