import Vue from 'vue'
import {ComponentFactory} from '../core/ComponentWrapper';

const ListEvents = [
    'selection-changing', 'selection-change',
    'item-down', 'item-up', 'item-click', 'item-double-click',
    'render-updating', 'render-update', 'size-change', 'caret-change',
    'collection-change'
];

const ListMethods = [
    'getItemElementAt',
    'getItemElement',
    'getVisibleIndices',
    'getVisibleElements',
    'getElementAll',
    'scrollToIndex',
    'scrollToItem',
    'scrollToTop',
    'scrollToBottom',
    'getItemStart',
    'getItemSize',
    'setItemSize',
    'invalidate',
    'validateNow',
    'syncScroll',
];

const defaultMethods = {};

function setupMethods(name) {
    defaultMethods[name] = function () {
        if (this.instance) {
            return this.instance[name].apply(this.instance, arguments);
        }
        return null;
    };
}

ListMethods.forEach((p) => {
    setupMethods(p);
});

function getValue(item, field) {
    if (!item) {
        return null;
    }

    if (!field) {
        return item;
    }

    if (field instanceof Function) {
        return field(item)
    } else if (!item) {
        return ''
    } else if (typeof item !== 'object') {
        return item;
    }

    return vrix.util.getValue(item, field);
}


export default Vue.extend({
    name: 'listable',
    provide() {
        return {owner: this};
    },
    props: {
        value: undefined,
        items: {
            type: Array,
        },
        scrollTarget: {
            type: [String, HTMLElement]
        },
        /**
         * 행 높이를 설정
         * 기본값 : 40
         */
        rowHeight: {
            type: Number,
            default: 40
        },
        autoHeight: {
            type: Boolean,
            default: false
        },
        /**
         * 항목 리스트의 행 개수를 설정.
         */

        rowCount: {
            type: Number,
            default: -1
        },
        /**
         * 'layout = 'grid' 일때 컬럼 개수
         */
        columnCount: {
            type: Number,
            default: 0
        },

        columnWidth: {
            type: Number,
            default: 0
        },

        /**
         * 각 항목의 label로 표시할 객체 속성 이름
         */
        labelField: {
            type: [String, Function],
            default: 'label'
        },
        /**
         * 각 항목의 value로 표시할 객체 속성 이름
         */
        valueField: {
            type: [String, Function],
        },

        /**
         * 리스트 레이아웃 설정
         * 'vertical', 'horizontal', 'grid', 'flow'
         */
        layout: {
            type: String,
            default: 'vertical'
        },
        horizontalGap: {
            type: Number,
            default: 0
        },
        verticalGap: {
            type: Number,
            default: 0
        },
        /**
         * 각 행 또는 컬럼이 다른 크기 가질 수 있는지 여부
         * true : 항목을 표현한 엘리먼트 크기
         * false : rowHeight 값
         */
        autoSize: {
            type: Boolean,
            default: false
        },
        /**
         * 최소 선택 개수
         * default: 0
         */
        minSelection: {
            type: Number,
            default: 0
        },
        /**
         * 최대 선택 개수
         * default: 1
         */
        maxSelection: {
            type: Number,
            default: 1
        },
        itemFactory: {
            type: Object
        },
        /**
         * 아이템 엘리먼트의 너비를 계산할 때 호출됩니다.
         *
         */
        measureElementWidth: {
            type: Function
        },
        /**
         * 아이템 엘리먼트의 높이를 계산할 때 호출됩니다.
         *
         */
        measureElementHeight: {
            type: Function
        },
        selectOnClick: {
            type: Boolean,
            default: true
        },
        selectOnFocus: {
            type: Boolean,
            default: false
        },
        selectOnKeys: {
            type: Array,
            default: () => {
                return [13, 32]
            }
        },
        useCtrlOnSelection: {
            type: Boolean,
            default: false
        },
        selectedValue: null,
    },
    data() {
        return {
            instance: null,
            selectedItems: [],
            selectedItem: null,
            selectedIndices: [],
            selectedIndex: -1,
            lockSelect: false,
            deferredFunc: null,
        }
    },
    computed: {
        collection() {
            return this.items && this.instance && this.instance.collection;
        },
        itemStaticClass() {
            return 'layout--' + this.layout;
        },
        itemFactoryWrapper() {
            if (!this.itemFactory) {
                throw new Error(`'itemFactory' is null`);
            }
            const self = this;
            return ComponentFactory.factoryWrapper(this, this.itemFactory, {
                create(data) {
                    const component = ComponentFactory.prototype.create.apply(this, [data]);
                    component.$scopedSlots = self.$scopedSlots;
                    if (component.$el && data.staticClass) {
                        component.$el.classList.add(data.staticClass);
                    }
                    return component;
                }
            });
        }
    },
    watch: {
        instance: {
            immediate: true,
            handler(instance) {
                if (instance) {
                    ListEvents.forEach(type => {
                        instance.addEventListener(type, this.onListInstanceEvents);
                    });
                    const exceptions = ['itemFactory', 'scrollTarget'];
                    for (let p in this.$props) {
                        if (exceptions.indexOf(p) < 0 && p in instance) {
                            instance[p] = this.$props[p];
                        }
                    }
                    this._isInited = true;
                    if (this.deferredFunc) {
                        this.deferredFunc();
                        this.deferredFunc = null;
                    }
                }
            }
        },
        value: {
            immediate: true,
            handler(value, old) {
                if ((value === undefined && old === undefined) || this.selectedItem === value) {
                    return;
                }
                if (value) {
                    this.pendingData = {value};
                }
                this.observeItems()
                    .then(changed => {
                        if (!changed) {
                            if (value && !Array.isArray(value)) {
                                value = [value];
                            }
                            this.internalSelectItems(value);
                        }
                    });
            },
        },
        selectedValue: {
            immediate: true,
            handler(value, old) {
                if ((value === undefined && old === undefined) || this.selectedItem && this.itemToValue(this.selectedItem) === value) {
                    return;
                }

                if (value != null) {
                    this.pendingData = {selectedValue: value};
                }

                this.observeItems()
                    .then(changed => {
                        if (!changed) {
                            this.selectItem(this.findItem(value));
                        }
                    });
            },
        },
        items: {
            deep: true,
            handler(value) {
                this.instance.items = value;
                this.instance.validateNow();
                this.$emit('update:items', value);
            }
        },
        rowHeight(value) {
            this.instance.rowHeight = value;
        },
        selectedItem(value) {
            this.selectItem(value);
        },
        selectedItems(value) {
            this.internalSelectItems(value);
        },
        selectedIndex(value) {
            this.internalSelectItems(this.items[value]);
        },
        selectedIndices(value) {
            this.internalSelectItems((value || []).map(i => this.items[i]));
        },
    },
    beforeMount() {
        const target = this.scrollTarget;
        if (target) {
            //상속된 클래스 'mounted' 실행후 호출하기 위한.
            this.$once('hook:mounted', () => {
                let element = null;
                if (target instanceof HTMLElement) {
                    element = target;
                } else if (typeof target === 'string') {
                    element = document.querySelector(target);
                }

                if (element) {
                    this.instance.scrollTarget = element;
                }
            });
        }
    },
    beforeDestroy() {
        if (this.instance) {
            ListEvents.forEach(type => {
                this.instance.removeEventListener(type, this.onListInstanceEvents);
            });
            this.instance.destroy();
        }
    },
    methods: {
        ...defaultMethods,
        /**
         * 지정된 항목에 대해 문자열을 반환합니다.
         * labelField(문자열) 이름이 같은 속성의 값
         * labelField(함수) 함수 반환 값
         * @param item
         * @param field
         * @returns {string}
         */
        itemToLabel(item, field) {
            return getValue(item, field || this.labelField);
        },
        itemToValue(item, field) {
            return getValue(item, field || this.valueField || this.labelField);
        },
        findItem(value) {
            return this.items && this.items.find(item => value === this.itemToValue(item));
        },
        contains(item) {
            if (this.instance && this.instance.collection) {
                return this.instance.collection.contains(item);
            }
            return !!(this.items && this.items.indexOf(item) >= 0);
        },
        selectItem(item) {
            if (this.selectedItem !== item || (this.instance && this.instance.selectedItem !== item)) {
                this.internalSelectItems(item ? [item] : null, true);
            }
        },
        selectItems(value) {
            return this.internalSelectItems(value, true);
        },
        internalSelectItems(value, outsideCall = false) {
            if (this.lockSelect && !outsideCall) {
                return;
            }

            if (!this.instance || !this._isInited) {
                this.deferredFunc = () => {
                    this.internalSelectItems(value);
                };
                return;
            }

            if (value != null) {
                value = Array.isArray(value) ? value : [value];
            }

            if (!value || (this.items && this.contains(value) && !vrix.util.compareArray(this.selectedItems, value))) {
                this.instance.selectedItems = value;
            }

            this.pendingData = null;
        },
        observeItems() {
            return new Promise(resolve => {
                let changed = false;
                const handler = () => {
                    changed = true;
                };
                this.$on('update:items', handler);
                this.$nextTick(() => {
                    this.$off('update:items', handler);
                    resolve(changed)
                })
            });
        },
        /**
         * listInstance 이벤트 핸들
         *
         * @param event
         */
        onListInstanceEvents(event) {
            if (event.type === 'selection-change') {
                this.lockSelect = true;

                const list = this.instance;
                this.selectedItems = list.selectedItems;
                this.selectedItem = list.selectedItem;
                this.selectedIndices = list.selectedIndices;
                this.selectedIndex = list.selectedIndex;
                this.$emit('input', this.maxSelection <= 1 ? this.selectedItem : this.selectedItems);
                this.$emit('update:selectedValue', this.itemToValue(this.selectedItem));

                this.$nextTick(() => {
                    this.lockSelect = false;
                });
            } else if (event.type === 'collection-change') {

                if (event.detail.kind === 'reset') {
                    const data = this.pendingData;
                    if (data && (data.value || data.selectedValue)) {
                        let found = null;
                        if (data.value) {
                            found = data.value;
                        } else if (data.selectedValue != null) {
                            found = this.findItem(data.selectedValue);
                        }
                        this.internalSelectItems(found);
                    }
                }
            }
            this.$emit(event.type, event);
        },
    },
});
