import DropIndicator from './TreeDropIndicator.js';
import ListDraggable from '../vxlist/ListDraggable';

export default {
    name: 'tree-draggable',
    mixins: [ListDraggable],
    props: {
        draggableSelector: {
            type: [String, Function],
            default: '.vx-tree__item'
        },
        droppableSelector: {
            type: [String, Function],
            default: '.vx-tree__item'
        },
        dropIndicator: {
            type: Object,
            default: () => DropIndicator
        }
    },
    data() {
        return {};
    },
    computed: {},
    watch: {},
    mounted() {
    },
    destroyed() {
    },
    methods: {

        /**
         * 드랍위치 정보를 계산합니다.
         * @param detail
         * @returns {{dragIndex: Number, dragItem: *, dropIndex: Number, dropItem: *, insertIndex: Number}}
         */
        calculateDropPosition (detail) {
            const GAP_RATIO = 0.30;
            const LAST_RATIO = 0.10;

            let event = detail.trigger;
            let dragItem = detail.dragSource;
            let dragIndex = this.collection.indexOf(dragItem);
            let overIndex = this.instance.mouseEventToIndex(event);
            if (overIndex < 0) {
                return null;
            }

            let overElement = detail.overContainer;
            let overItem = this.collection.get(overIndex);
            let overRect = overElement.getBoundingClientRect();
            let overY = Math.round(event.clientY - overRect.top);
            let overHeight = overRect.height;

            let topRatio = overY / overHeight;
            let bottomRatio = 1 - topRatio;

            let insertParentItem = this.getParentNode(overItem);
            let insertChildren = this.getChildren(insertParentItem);
            let insertIndex = insertChildren.indexOf(overItem);

            if (topRatio < GAP_RATIO) {

            } else if (bottomRatio < GAP_RATIO) {

                if (this.isOpenNode(overItem) && this.hasChildren(overItem)) {
                    insertParentItem = overItem;
                    insertIndex = 0;
                } else {
                    //마지막 인덱스일때.
                    if (bottomRatio < LAST_RATIO && insertParentItem && this.collection.length - 1 === overIndex) {
                        insertParentItem = null;
                        insertChildren = this.getChildren(insertParentItem) || [];
                        insertIndex = insertChildren.length;
                    } else {
                        insertIndex = insertIndex + 1;
                    }
                }
            } else {
                insertParentItem = overItem;
                insertChildren = this.getChildren(insertParentItem) || [];
                insertIndex = insertChildren.length;
            }

            return {
                dragIndex: dragIndex,
                dragItem: dragItem,
                dropIndex: overIndex,
                dropItem: overItem,

                insertParentItem: insertParentItem,
                insertIndex: insertIndex,
            };
        },

        /**
         * 드랍된 아이템을 적용합니다.
         */
        applyDroppedItem (detail) {

            let collection = this.collection;
            let dropPosition = detail.dropPosition;

            let dragItem = dropPosition.dragItem;

            let dragParentItem = collection.getParentNode(dragItem);
            let dragChildIndex = collection.getNodeIndex(dragItem);

            let insertParentItem = dropPosition.insertParentItem;
            let insertChildIndex = dropPosition.insertIndex;

            //같은 부모
            if (dragParentItem === insertParentItem && dragChildIndex < insertChildIndex) {
                insertChildIndex = Math.max(insertChildIndex - 1, 0);
            }

            this.removeNode(dragParentItem, dragItem);
            this.addNodeAt(insertParentItem, dragItem, insertChildIndex);
        },


        /**
         * 드랍가능한 영역인지 확인합니다.
         * @returns {Boolean}
         */
        validDropPosition (dropPosition) {
            let dragNode = dropPosition.dragItem;
            let dropNode = dropPosition.dropItem;
            let dropParentNode = dropPosition.insertParentItem;
            //부모의 노드의 상대 인덱스
            let insertIndex = dropPosition.insertIndex;

            //1. 드래그 위치와 드랍위치가 같을 때
            //2. 드랍위치가 자식들일 때.
            let node = dropNode;
            while (node) {
                if (node === dragNode) {
                    return false;
                }
                node = this.getParentNode(node);
            }

            //3. 같은 부모이면서 드랍될 인덱스가 현재와 같을 때
            if (this.getParentNode(dragNode) === dropParentNode) {
                let children = this.getChildren(dropParentNode);
                let delta = insertIndex - children.indexOf(dragNode);
                if (delta >= 0 && delta <= 1) {
                    return false;
                }
            }
            return true;
        },


        /**
         * 드래그 오버 핸들러
         * @param event
         */
        onDragOver(event) {
            ListDraggable.methods.onDragOver.apply(this, arguments);
            const dropItem = event.detail.dropPosition && event.detail.dropPosition.dropItem;
            if (this._pendingOpenId) {
                clearTimeout(this._pendingOpenId);
                this._pendingOpenId = null;
            }
            const DELAY_OPEN_TIME = 600;
            if (!this.isOpenNode(dropItem) && this.hasChildren(dropItem)) {
                this._pendingOpenId = setTimeout(function () {
                    this.openNode(dropItem);
                    this.validateNow();
                }.bind(this), DELAY_OPEN_TIME);
            }
        }
    },
}
