import Dropdownable from '../mixins/dropdownable'
import VxList from '../vxlist/VxList'

export default {
    name: 'vx-combobox',
    mixins: [Dropdownable],
    props: {
        value: undefined,
        items: {
            type: Array
        },
        labelField: {
            type: [String, Function],
            default: 'label'
        },
        valueField: {
            type: [String, Function],
        },
        rowHeight: {
            type: Number,
            default: 40
        },
        rowCount: {
            type: Number,
            default: 7
        },
        minSelection: {
            type: Number,
            default: 0
        },
        maxSelection: {
            type: Number,
            default: 1
        },
        selectedValue: null
    },
    computed: {
        selectedLabels() {
            const items = this.selectedItems || [];
            return items.map(item => this.itemToLabel(item));
        },
        selectedValues() {
            const items = this.selectedItems || [];
            return items.map(item => this.itemToValue(item));
        },
        selectedItem() {
            return this.selectedItems && this.selectedItems.length > 0 ? this.selectedItems[0] : null;
        },
        selectedIndex() {
            return (this.selectedItem) ? this.items.indexOf(this.selectedItem) : -1;
        },
    },
    watch: {
        value: {
            immediate: true,
            handler(value, old) {
                if ((value === undefined && old === undefined) || this.selectedItem === value) {
                    return;
                }
                this.selectItems(value);
            }
        },
        selectedValue: {
            immediate: true,
            handler(value, old) {
                if ((value === undefined && old === undefined) || this.selectedItem && this.itemToValue(this.selectedItem) === value) {
                    return;
                }
                this.selectValue(value);
            }
        },
        minSelection: {
            immediate: true,
            handler(value) {
                if (value > 0) {
                    this.forceBuildContent();
                }
            }
        }
    },
    methods: {
        getSelectedItem() {
            return this.selectedItems && this.selectedItems[0] || null;
        },
        getSelectedValue() {
            return this.itemToValue(this.getSelectedItem());
        },
        itemToLabel(item) {
            return VxList.options.methods.itemToLabel.apply(this, [item, this.labelField]);
        },
        itemToValue(item) {
            return VxList.options.methods.itemToValue.apply(this, [item, this.valueField]);
        },
        selectItems(value) {
            if (value && !Array.isArray(value)) {
                value = [value];
            }
            if (!this.forceBuildContent()) {
                this.$refs.dropdownList.selectItems(value);
            }
        },
        selectValue(value) {
            if (!this.forceBuildContent()) {
                if (!this.items || this.selectedItem && this.itemToValue(this.selectedItem) === value) {
                    return;
                }
                this.selectItems(this.items.find(item => value === this.itemToValue(item)));
            }
        },
        genDropdownContent() {
            const width = this.calculatedMinWidth();
            return this.$createElement(VxList, {
                staticClass: 'vx-combobox__dropdown-content',
                style: {
                    width: `${width}px`,
                    minWidth: `${width}px`,
                    minHeight: `${this.rowHeight}px`
                },
                props: {
                    ...this.$props
                },
                on: {
                    'input': this.onInput,
                    'update:selectedValue': this.onUpdateSelectedValue,
                    'selection-changing': this.onListEvents,
                    'selection-change': this.onListEvents,
                    'item-down': this.onListEvents,
                    'item-up': this.onListEvents,
                    'item-click': this.onListEvents,
                    'collection-change': this.onListEvents
                },
                scopedSlots: this.$scopedSlots,
                ref: 'dropdownList'
            });
        },
        forceBuildContent() {
            if (!this.$refs.dropdownList) {
                this.$nextTick(() => {
                    if (!this.$refs.dropdown) {
                    } else {
                        this.$refs.dropdown.forceBuildContent();
                    }
                });
                return true;
            }
            return false;
        },
        onListEvents(event) {
            const type = event.type;
            if (type === 'selection-change') {
                this.selectedItems = event.detail.selectedItemsTo;
            }
            this.$emit(type, event);
        },
        onInput(value) {
            this.$emit('input', value);
        },
        onUpdateSelectedValue(value) {
            this.$emit('update:selectedValue', value);
        },
        onDropdownOpening(event) {
            const dropdownList = this.$refs.dropdownList;
            dropdownList.syncScroll();
            if (this.selectedIndex >= 0) {
                dropdownList.scrollToIndex(this.selectedIndex);
            }
            dropdownList.invalidate();
            dropdownList.validateNow();
        }
    }
}
