import DropIndicator from './ListDropIndicator.js';
import {unobserve} from '../util/unobserve';

const DRAG_EVENTS = [
    'drag-ready', 'drag-start', 'drag-move',
    'drag-over', 'drag-out',
    'drag-enter', 'drag-leave',
    'drag-end', 'drop',
    'drag-cancel',
    'drag-mirror-create', 'drag-mirror-remove'
];

export default {
    name: 'list-draggable',
    props: {
        draggable: {
            type: Boolean,
            default: false
        },
        droppable: {
            type: Boolean,
            default: false
        },
        draggableSelector: {
            type: [String, Function],
            default: '.vx-list__item'
        },
        droppableSelector: {
            type: [String, Function],
            default: '.vx-list__item'
        },
        dropIndicator: {
            type: Object,
            default: () => DropIndicator
        }
    },
    data() {
        return {
            dragManager: null,
            dragging: false,
            dragEntering: false
        };
    },
    computed: {},
    watch: {
        draggable: {
            immediate: true,
            handler(value, old) {
                if (value) {
                    this.$nextTick(() => {
                        this.dragManager = unobserve(new vrix.ui.DragManager(this.$el, {
                            scrollTarget: this.$el,
                            draggable: this.draggableSelector,
                            droppable: this.droppableSelector,
                            dragSource: function (element, event) {
                                return element.__vue__.item;
                            },
                        }));
                    })
                } else {
                    this.dragManager = null;
                }
            }
        },
        dragManager(manager, old) {
            if (old) {
                old.destroy();
                DRAG_EVENTS.forEach(type => {
                    old.removeEventListener(type, this.onDragManagerEvent)
                });
            }

            if (manager) {
                //리스너 등록.
                DRAG_EVENTS.forEach(type => {
                    manager.addEventListener(type, this.onDragManagerEvent);
                });
            }
        }
    },
    mounted() {
    },
    destroyed() {
    },
    methods: {

        genIndicator() {
            if (!this.dragging || !this.dragEntering) {
                return null;
            }

            const children = [];
            if (this.$scopedSlots.dropIndicator) {
                children.push(this.$scopedSlots.dropIndicator({
                    item: this.dragManager.dragSource
                }));
            }

            return this.$createElement(this.dropIndicator, {
                staticClass: this.$options.name + '__indicator',
                ref: 'indicator'
            }, children);
        },

        /**
         * 드랍위치 정보를 계산합니다.
         * @param detail
         * @returns {{dragIndex: Number, dragItem: *, dropIndex: Number, dropItem: *, insertIndex: Number}}
         */
        calculateDropPosition: function (detail) {
        },

        /**
         * 드랍된 아이템을 적용합니다.
         */
        applyDroppedItem: function (detail) {

        },


        /**
         * 드랍가능한 영역인지 확인합니다.
         * @returns {Boolean}
         */
        validDropPosition: function (dropPosition) {
            return true;
        },

        validDragEnter: function (dropPosition, old) {
            if (dropPosition && old) {
                for (let p in dropPosition) {
                    if (dropPosition[p] != old[p]) {
                        return false;
                    }
                }
                return true;
            }
            return false;
        },

        showDropIndicator: function (detail) {
            let indicator = this.$refs.indicator;
            if (indicator) {
                indicator.showDropIndicator(detail);
            }
        },

        hideDropIndicator: function (destroy) {

        },

        onDragReady(event) {

        },
        /**
         * 드래그 시작 핸들러
         * @param event
         */
        onDragStart(event) {
            if (!event.defaultPrevented) {
                this.dragging = true;
            }
        },

        /**
         * 드래그 오버 핸들러
         * @param event
         */
        onDragOver(event) {
            const detail = event.detail;
            const dropPosition = this.calculateDropPosition(detail);
            detail.dropPosition = dropPosition;
            if (!this.dragManager.dragScrolling && dropPosition && this.validDropPosition(dropPosition) && !event.defaultPrevented) {
                if (!this.validDragEnter(dropPosition, this._pendingDropPosition)) {
                    this.dragManager.cancelDragEnter();
                }
                this._pendingDropPosition = dropPosition;
                event.preventDefault();
            } else {
                this._pendingDropPosition = null;
            }
        },

        onDragOut(event) {
            event.detail.dropPosition = this._pendingDropPosition;
            //this._dispatchItemDragEvent(event);
            this._pendingDropPosition = null;
        },

        onDragEnter(event) {
            let detail = event.detail;
            detail.dropPosition = this._pendingDropPosition;

            //TODO 소스 정리 필요!!!
            this.$emit(`item-${event.type}`, event);

            if (event.defaultPrevented) {
                return;
            }
            this.dragEntering = true;
            this.showDropIndicator(detail);
        },

        onDragLeave(event) {
            event.detail.dropPosition = this._pendingDropPosition;
            this.dragEntering = false;
            this.hideDropIndicator();
            this._pendingDropPosition = null;
        },

        onDragEnd(event) {
            event.detail.dropPosition = this._pendingDropPosition;

            this.dragging = false;
            this.dragEntering = false;
            this.hideDropIndicator(true);
            this._pendingDropPosition = null;
        },

        onDragCancel(event) {
            event.detail.dropPosition = this._pendingDropPosition;
            this.dragging = false;
            this.dragEntering = false;
            this.hideDropIndicator(true);
            this._pendingDropPosition = null;
        },

        onDrop(event) {
            if (this.droppable) {
                let detail = event.detail;
                detail.dropPosition = this._pendingDropPosition;
                //TODO 소스 정리 필요!!!
                this.$emit(`item-${event.type}`, event);
                if (!event.defaultPrevented) {
                    this.applyDroppedItem(detail);
                }
            }
        },

        onDragMirrorCreate(event) {
        },

        onDragMirrorRemove(e) {
        },

        onDragManagerEvent(event) {

            if (event.cancelable && (event.type !== 'drag-enter' && event.type !== 'drop')) {
                this.$emit(`item-${event.type}`, event);
            }

            //TODO 소스 줄이기!!!
            switch (event.type) {
                case 'drag-ready':
                    this.onDragReady(event);
                    break;
                case 'drag-start':
                    this.onDragStart(event);
                    break;
                case 'drag-over':
                    this.onDragOver(event);
                    break;
                case 'drag-out':
                    this.onDragOut(event);
                    break;
                case 'drag-enter':
                    this.onDragEnter(event);
                    break;
                case 'drag-leave':
                    this.onDragLeave(event);
                    break;
                case 'drag-end':
                    this.onDragEnd(event);
                    break;
                case 'drop':
                    this.onDrop(event);
                    break;
                case 'drag-cancel':
                    this.onDragCancel(event);
                    break;
                case 'drag-mirror-create':
                    this.onDragMirrorCreate(event);
                    break;
                case 'drag-mirror-remove':
                    this.onDragMirrorRemove(event);
                    break;
            }

            if (!event.cancelable) {
                this.$emit(`item-${event.type}`, event);
            }
        }
    },
}
