const reRegExpChar = /[\\^$.*+?()[\]{}|]/g;
const reHasRegExpChar = RegExp(reRegExpChar.source);

export function escapeRegExp(string = '') {
    return reHasRegExpChar.test(string) ? string.replace(reRegExpChar, '\\$&') : string;
}

/**
 * Copy an object.
 *
 * @public
 * @function
 * @param {*} source
 * @param {boolean} [deep]
 * @returns {*} Clone.
 */
export function copy(source, deep) {
    if (source == null) return source;

    if (source instanceof Date) {
        return new Date(source.getTime());
    }

    if (source instanceof RegExp) {
        let text = String(source),
            slashIndex = text.lastIndexOf('/');
        return new RegExp(
            text.slice(1, slashIndex),
            text.slice(slashIndex + 1)
        );
    }

    if (Array.isArray(source)) {
        let n = source.length;
        let o = new Array(n);
        for (let i = n - 1; i >= 0; i--) {
            o[i] = deep ? copy(source[i], deep) : source[i];
        }
        return o;
    }

    if (typeof (source) === 'object') {
        let o = {};
        for (let k in source) {
            o[k] = deep ? copy(source[k], deep) : source[k];
        }
        return o;
    }
    return source;
}

/**
 * Clone an object.
 * @param source
 * @returns {*}
 */
export function clone(source) {
    return copy(source, true)
}


export function compareArray(v1, v2) {
    const n1 = v1 && v1.length || 0;
    const n2 = v2 && v2.length || 0;
    if (n1 !== n2) {
        return false
    }
    for (let i = 0; i < n1; i++) {
        if (v1[i] !== v2[i]) {
            return false
        }
    }
    return true
}

/**
 * Mix objects.
 * 오브젝트 타입만 적용됩니다.(Array는 제외)
 * @param args
 * @returns {Object}
 *
 * @example
 * let d = {a: 1, b: 2};
 * vrix.util.mixin(d, {c: 3, d: 4}, {b: 'A', e: 'E'}, {override: true});
 * console.log(d); d = {a: 1, b: "A", c: 3, d: 4, e: "E"}
 */

export function mixin(...args) {
    let numArgs = args.length;
    let target = args[0];
    if (numArgs < 2) {
        return target;
    }

    let numSource = numArgs;
    let options = args[numArgs - 1];
    if (options && Object.keys(options).length <= 3 && ('overwrite' in options || 'copy' in options || 'deep' in options)) {
        numSource = numArgs - 1;
    } else {
        options = {overwrite: true, copy: false, deep: false};
    }


    let overwrite = !!options.overwrite;
    let isCopy = !!options.copy;
    let isDeep = !!options.deep;

    for (let i = 1; i < numSource; ++i) {
        let source = args[i];
        if (typeof source !== 'object' || Array.isArray(source)) {
            console.warn('`source` is not an object.', source);
            continue;
        }

        for (let p in source) {
            let t = target[p];
            if (t !== undefined && !overwrite) {
                continue;
            }
            let s = isCopy ? copy(source[p], true) : source[p];
            if (isDeep && typeof t === 'object' && typeof s === 'object') {
                mixin(t, s, options);
            } else {
                target[p] = s;
            }
        }
    }
    return target;
}

export function withBehavior(element, ...behaviors) {
    const prototype = element.prototype;
    behaviors.forEach((behavior) => {
        Object.getOwnPropertyNames(behavior).forEach((name) => {
            Object.defineProperty(prototype, name, Object.getOwnPropertyDescriptor(behavior, name));
        });
    });
    return element;
}

export function cacheFunction(callback) {
    let cache = {};
    return function (value) {
        let result = cache[value];
        if (!result) {
            result = callback(value)
        }
        return result;
    }
}

/**
 *
 * @param func
 * @param thisTarget
 * @param delay
 * @returns {function(...[*]=)}
 */
export function debounceFunction(func, thisTarget = null, delay = 100) {
    let timerId = null;
    return function () {
        if (timerId) {
            clearTimeout(timerId);
        }
        const args = arguments;
        timerId = setTimeout(function () {
            func.apply(thisTarget, args);
        }, delay);
    }
}

/**
 *
 * @param func
 * @param thisTarget
 * @param delay
 * @returns {function(...[*]=)}
 */
export function throttleFunction(func, thisTarget = null, delay = 100) {
    let isAccess = true;
    return function () {
        if (!isAccess) {
            return;
        }

        isAccess = false;
        func.apply(thisTarget, arguments);
        setTimeout(function () {
            isAccess = true;
        }, delay);
    }
}


const internalParsePath = cacheFunction((value) => {
    return value && ((value.indexOf('.') !== -1 && value.split('.')) || [value]);
});

export function parsePath(path) {
    if (Array.isArray(path)) {
    } else {
        const type = typeof path;
        switch (type) {
            case 'number':
                path += '';
            case 'string':
                path = internalParsePath(path);
                break;
            default: {
                throw new TypeError(`path must be a Number/String/Array, received ${type} instead.`)
            }
        }
    }
    return path;
}

/**
 *
 * @param {Object} data
 * @param {Array, String} path
 * @returns {*}
 */
export function getValue(data, path) {
    path = parsePath(path);
    const num = path && path.length || 0;
    let o = data;
    for (let i = 0; i < num; i++) {
        let field = path[i];
        if (field in o) {
            o = o[field];
        } else {
            return null;
        }
    }
    return o;
}

/**
 *
 * @param data
 * @param path
 * @param value
 * @returns {boolean}
 */
export function setValue(data, path, value) {
    path = parsePath(path);
    const num = path && path.length || 0;
    let o = data;
    let i = 0;
    for (; i < num - 1; i++) {
        let field = path[i];
        if (field in o) {
            o = o[field];
        } else {
            //error
            o = null;
            break;
        }
    }

    if (o && num > 0 && o[path[i]] !== value) {
        o[path[i]] = value;
        return true;
    }
    return false;
}
