import * as util from './dateutil';

function sign(value) {
    return value === 0 ? 0 : value > 0 ? 1 : -1;
}

class BaseProvider {
    constructor(table, x, y, minDate = new Date(1970, 0, 1), maxDate = new Date(2101, 11, 31)) {
        this.table = table;
        this.x = x;
        this.y = y;
        this.count = x * y;
        this.minDate = minDate;
        this.maxDate = maxDate;
        this.current = null;
        this.items = [];
    }

    updateItems(date) {
        if (this.validItems(date) || this.minDate > date || this.maxDate < date) {
            return this.items;
        }
        this.current = date;
        this.items = this.createItems(date);
        return this.items;
    }

    validItems(date) {
        return !!this.findItem(date);
    }

    createItems(date) {

    }

    createItem(date) {
        const toValue = this.constructor.toValue;
        return {
            source: date,
            fullValue: toValue(date),
            allowed: !this.table.allowDates || !!this.table.allowDates(date),
            tense: sign(toValue(date) - toValue(this.table.today)),
            extra: false
        };
    }

    findItem(date) {
        const value = this.constructor.toValue(date);
        return this.items.find(item => value === item.fullValue);
    }

    move(value) {

    }

    equals(date) {
        const toValue = this.constructor.toValue;
        return toValue(this.current) === toValue(date);
    }
}

export class YearProvider extends BaseProvider {
    constructor(table, x, y) {
        super(table, x, y);
    }

    getBlockIndex(date) {
        return Math.floor((date.getFullYear() - this.minDate.getFullYear()) / this.count);
    }

    createItems(date) {
        const min = this.minDate.getFullYear();
        const first = Math.max(min, min + this.count * this.getBlockIndex(date));
        const items = [];
        const start = new Date(first, 0, 1);
        for (let i = 0; i < this.count; i++) {
            items.push(this.createItem(util.cloneDate(start)));
            start.setFullYear(start.getFullYear() + 1);
        }
        return items;
    }

    createItem(date) {
        const item = super.createItem(date);
        item.value = +this.constructor.toValue(date);
        item.year = date.getFullYear();
        return item;
    }

    move(value) {
        const min = this.minDate.getFullYear();
        const first = Math.max(min, min + this.count * (this.getBlockIndex(this.current) + value));
        return this.updateItems(new Date(first, 0, 1));
    }

    static toValue(date) {
        return util.toYYYY(date);
    }
}

export class MonthProvider extends BaseProvider {
    createItems(date) {
        const items = [];
        const start = new Date(date.getFullYear(), 0, 1);
        for (let i = 0; i < this.count; i++) {
            items.push(this.createItem(util.cloneDate(start)));
            start.setMonth(start.getMonth() + 1);
        }
        return items;
    }

    createItem(date) {
        const item = super.createItem(date);
        item.value = date.getMonth() + 1;
        item.year = date.getFullYear();
        item.month = date.getMonth() + 1;
        return item;
    }

    move(value) {
        return this.updateItems(new Date(this.current.getFullYear() + value, 0, 1));
    }

    static toValue(date) {
        return util.toYYYYMM(date);
    }
}

export class DayProvider extends BaseProvider {
    createItems(date) {
        const items = [];
        const start = util.getMonthStart(date);
        for (let i = 0; i < this.count; i++) {
            items.push(this.createItem(util.cloneDate(start)));
            start.setDate(start.getDate() + 1);
        }
        return items;
    }

    createItem(date) {
        const item = super.createItem(date);
        item.value = date.getDate();
        item.year = date.getFullYear();
        item.month = date.getMonth() + 1;
        item.day = date.getDate();
        item.weekday = date.getDay();
        item.extra = util.toYYYYMM(date) !== util.toYYYYMM(this.current);
        return item;
    }

    validItems(date) {
        return util.toYYYYMM(date) === util.toYYYYMM(this.current);
    }

    move(value) {
        const date = util.cloneDate(this.current);
        date.setDate(1);
        date.setMonth(date.getMonth() + value);
        return this.updateItems(date);
    }

    static toValue(date) {
        return util.toYYYYMMDD(date);
    }
}
