import * as components from './vendor/vue';
import {ComponentFactory} from './vendor/vue/core/ComponentWrapper'

const vrixVue = {
    install(Vue, options) {
        const comps = components || {};
        for (const key in comps) {
            Vue.component(key, comps[key])
        }
    },
    version: '1.0.0',
    ComponentFactory
};

if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(vrixVue, {
        components
    })
}

export default vrixVue;
