export default {
    name: 'listitemable',
    inject: {
        owner: {
            default: null
        }
    },
    props: {
        item: null,
        labelField: {
            type: [String, Function],
        },
        valueField: {
            type: [String, Function],
        }
    },
    data() {
        return {
            index: -1,
            state: () => ({}),
        }
    },
    computed: {
        staticClass() {
            return 'vx-list__item';
        },
        itemLabel() {
            return this.owner.itemToLabel(this.item);
        },
        itemValue() {
            return this.owner.itemToValue(this.item);
        },
        itemAttrs() {
            return {
                selected: this.state.selected,
                active: this.state.active,
            }
        },
        scopedProps() {
            return {
                ...this.state,
                item: this.item,
                index: this.index,
                label: this.itemLabel,
                value: this.itemValue,
            }
        },
    },
}
