import Vue from 'vue';

export default Vue.extend({
    name: 'vx-input-control',
    inject: {
        autoComplete: {
            default: null
        }
    },
    methods: {
        genContent() {
            return [
                this.getInput(),
                this.genClear()
            ];
        },
        getInput() {
            if (this.$scopedSlots.input) {
                return this.$scopedSlots.input(this.scopedProps);
            }
            return this.$createElement('input', {
                staticClass: 'control__input',
                domProps: {
                    autocomplete: 'off'
                }
            });
        },
        genClear() {
            if (!this.autoComplete.clearButton) {
                return null;
            }

            const slot = this.autoComplete.$slots.clear;
            return this.$createElement('div', {
                staticClass: 'control__clear',
                on: {
                    click: this.clear
                },
            }, slot ? slot : 'X');
        },
        clear() {
            this.autoComplete.clear();
            this.autoComplete.focus();
        }
    },
    render(h) {
        return h('div', {
            staticClass: 'input-control',
            attrs: this.itemAttrs,
        }, this.genContent());
    }
})
