import Vue from 'vue';
import ListItemAble from '../mixins/listitemable';
import {DEFAULT_WEEK_LABELS} from './dateutil';

export default Vue.extend({
    name: 'vx-date-table-item',
    mixins: [ListItemAble],
    inject: {
        dateTable: {
            default: null
        }
    },
    props: {},
    computed: {
        staticClass() {
            return 'vx-date-table__item';
        },
        scopedProps() {
            return {
                ...this.state,
                item: this.item,
            }
        },
    },
    methods: {
        genContent() {
            return [this.genItem()];
        },
        genItem() {
            return this.$createElement('div', {
                staticClass: 'vx-date-table__item-label',
            }, [this.genLabel()]);
        },
        genLabel() {
            if (this.$scopedSlots.day) {
                return this.$scopedSlots.day(this.scopedProps);
            }
            return '' + this.item.value;
        }
    },
    render(h) {
        const clazz = {
            ['vx-' + (this.item.tense === 0 ? 'present' : this.item.tense > 0 ? 'future' : 'past')]: true,
            ['vx-disabled']: !this.item.allowed,
            ['vx-extra']: this.item.extra
        };

        if ('weekday' in this.item) {
            clazz['vx-' + DEFAULT_WEEK_LABELS[this.item.weekday].toLowerCase()] = true;
        }

        return h('div', {
            staticClass: this.staticClass,
            class: clazz,
            attrs: this.itemAttrs,
        }, this.genContent());
    }
})
